[class*="bar"]{
  
    border-radius: 5px 5px 0 0;
    background: var(--bg-bar-color);
    border-style:double;
    -webkit-transition: color 1s, -webkit-transform 0.5s, -webkit-filter 0.5s;
    transition: color 1s, -webkit-transform 0.5s, -webkit-filter 0.5s;
    -o-transition: transform 0.5s, filter 0.5s, color 1s ;
    transition: transform 0.5s, filter 0.5s, color 1s ;
    transition: transform 0.5s, filter 0.5s, color 1s, -webkit-transform 0.5s, -webkit-filter 0.5s; 
    text-align: center;
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    max-height: 100%;
    overflow:auto;
    font-size:50px


    }

  [class*="bar"]:hover{
    color: white;
    
    
  }

  [class*="bar"]:nth-child(even){
    background:#54b96a;
  }

  .Chart{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-column-gap: 10px;
    height: 80vh;
    width: 80vw;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
    grid-column:2;
    border-style: double;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: end;
    padding: 50px;
    background-color: var(--bg-chart-color) ;
    grid-row:2 / span 1;
    padding-bottom: 0px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size:auto;
    font-style: bold;
    align-items:flex-end;
    word-wrap: break-word;
   
}